* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  min-width: 320px;
  font-size: 16px;
}
img {
  max-width: 100%;
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
}

.sidebar-wrap {
  position: fixed;
  width: 300px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 480px;
}
.sidebar-wrap.team {
  background: linear-gradient(180deg, #679aff 0%, #913dff 100%);
}
.sidebar-wrap.projects {
  background: linear-gradient(180deg, #ffd600 0%, #ff993c 100%);
}
.sidebar-wrap.contacts {
  background: linear-gradient(180deg, #ff6794 0%, #ff4c64 100%);
}

.sidebar-wrap .main-logo {
  width: 120px;
  height: 120px;
  text-align: center;
  margin: 3rem 0;
  background: url(./images/main-logo-white.png) no-repeat center / contain;
}
.main-nav {
  width: 100%;
}
.main-menu li {
  margin: 2rem 0;
}
.main-menu li a {
  position: relative;
  display: flex;
  align-items: center;
  padding: 15px 20px;
  font-size: 1.5rem;
  margin: 0.5rem;
  font-family: "Comfortaa";
  color: #fff;
}
.main-menu li a::before {
  content: "";
  display: block;
  width: 22px;
  height: 22px;
  display: inline-block;
  border-radius: 50%;
  background: #fff;
  margin-right: 10px;
}
.main-menu li a.active::after {
  content: "";
  display: block;
  height: 150px;
  width: 50px;
  background: url(./images/bg-active-link.png) no-repeat center / contain;
  position: absolute;
  right: -8px;
  top: -50px;
}
.main-menu li a.active {
  background: rgb(255, 255, 255);
  border-radius: 88px;
  color: rgba(0, 0, 0, 0.8);
}

.main-menu li a.active.team::before {
  background: #747dff;
}
.main-menu li a.active.projects::before {
  background: #ffba1c;
}
.main-menu li a.active.contacts::before {
  background: #ff5879;
}

h1 {
  text-align: center;
}
